export default {
	data() {
		return {

		};
	},
	props: {
		config: {
			type: Object,
			default () {
				return {
					type: 0,
					title: "",
					back: false,
					renttype:1,
				}
			}
		}
	},
	computed: {
		height() {
			const {
				platform,
				statusBarHeight
			} = uni.getSystemInfoSync()
			let height = statusBarHeight + 4 //ios 24px
			if (platform.toLowerCase() == "android") {
				height += 4 //android 28px
			}
			return height + 38 + "px"
		},
		marginTop() {
			const {
				platform,
				statusBarHeight
			} = uni.getSystemInfoSync()
			let height = statusBarHeight + 4
			if (platform.toLowerCase() == "android") {
				height += 4
			}
			return height + "px"
		},
	},
	filters: {
		filters1: function(arg) {
			if (arg == 1) {
				return '出租';
			} else if (arg == 2) {
				return '出售';
			} else if (arg == 3) {
				return '求租';
			} else if (arg == 4) {
				return '求购';
			} else if (arg == 5) {
				return '找室友';
			} else {
				return '';
			}
		},
	},
	methods:{
		conduct_(){
			this.$emit("customConduct")	
		},
	}
}
